import React, { FC, LabelHTMLAttributes } from "react";

import { colors, Margins, spacingMap } from "../styleguide";
import Typography from "../Typography/Typography";
import uuid from "../utils/uuid";

import {
	StyledLabel,
	StyledRequired,
} from "./Label.styles";

type LabelAttributes = LabelHTMLAttributes<HTMLLabelElement>;

export interface LabelProps extends Margins, LabelAttributes {
	dataQA: string
	children?: string
	isRequired?: boolean
	isInvalid?: boolean
}

const Label: FC<LabelProps> = ({
	dataQA,
	id = uuid(),
	children,
	isRequired = false,
	isInvalid = false,
	className,
	htmlFor,
	mt,
	ml,
	mr,
	mb = spacingMap.xxs,
	m,
}) => (
	<StyledLabel
		data-qa={dataQA}
		className={className}
		htmlFor={htmlFor || id}
		id={id}
		mt={mt}
		ml={ml}
		mr={mr}
		mb={mb}
		m={m}
	>
		{ isRequired && <StyledRequired>*</StyledRequired> }
		{ children && (
			<Typography
				dataQA={`${dataQA}-typography`}
				variant="bodyCopyBold"
				component="span"
				color={isInvalid ? colors.destructive : colors.black}
			>
				{children}
			</Typography>
		)}
	</StyledLabel>
);

Label.displayName = "Label";
export default Label;
